<script>
import Header from './Header.vue';
import StartUse from './StartUse.vue';
import Chat from './Chat.vue';

export default {
  props: ['chatId'],
  data() {
    return {
      chatData: {},
      id: '',
    };
  },
  components:
  {
    Header,
    StartUse,
    Chat,
  },
  beforeUpdate(){
    //[ask] fetch??? beforeUpdate 
    if(this.id == this.chatId){
      return;
    }
    this.id = this.chatId;

    let url = 'http://my-json-server.typicode.com/yasuko8017/cherrichat-jsonserver/chatMsgData/'+ this.chatId;
    fetch(url)
      .then(response => {
        return response.json();
      })
      .then(jsonData => {
        this.chatData = jsonData;
      });
  },
};
</script>

<template>
  <div class="main">
    <Header/>
    <StartUse v-if="chatId == ''"/>
    <Chat v-if="chatId" :chatData="chatData"/>
  </div>
</template>


<style lang="scss">
.main {
  float: left;
  width: 580px;
  height: 100%;
}
</style>



